import {  Button } from '@arco-design/web-react';
import { useEffect } from 'react';
const div1 = document.createElement("div"); div1.innerText='';
const div2 = document.createElement("div"); div2.innerText='';
const div3 = document.createElement("div"); div3.innerText='';
const div4 = document.createElement("div"); div4.innerText='';
const div5 = document.createElement("div"); div5.innerText='';


const Time=()=>{
    useEffect(()=>{
      let dom = document.getElementById("father")
      setTimeout(() => {dom.appendChild(div1)}, 1000);
      setTimeout(() => {dom.appendChild(div2)}, 2000);
      setTimeout(() => {dom.appendChild(div3)}, 3000);
      setTimeout(() => {dom.appendChild(div4)}, 4000);
      setTimeout(() => {dom.appendChild(div5)}, 8000);
    },[])
    
  return <div id='father'>
    <Button type="primary"> click </Button>
  </div>
}
export default Time